<template>
  <v-card :loading="loading" :disabled="loading" elevation="0">
    <div v-for="(item,index) in items.data" :key="index+'scertiflist'" class="certifi-group mb-6 mt-2">

      <div class="d-flex justify-space-between align-center" >

      <p class="my-3">
        <v-icon small>mdi-certificate</v-icon>
        گواهی <strong>{{ index+1 }} :</strong></p>

      <v-btn  icon>
        <v-icon class="skill-btn" color="error" @click="deleteFromData(item.id)">mdi-close</v-icon>
      </v-btn>

      </div>

      <v-row class="py-3">
        <v-col v-if="item.title" cols="12" md="4">
          <div class="certifi-item">
            عنوان : <strong>{{ item.title }}</strong>
          </div>
        </v-col>
        <v-col v-if="item.reference" cols="12" md="4">
          <div class="certifi-item">
            مرجع : <strong>{{ item.reference }}</strong>
          </div>
        </v-col>
        <v-col v-if="item.state" cols="12" md="4">
          <div class="certifi-item">
            وضعیت : <strong>{{ item.state }}</strong>
          </div>
        </v-col>
        <v-col v-if="item.grade" cols="12" md="4">
          <div class="certifi-item">
            سطح : <strong>{{ item.grade }}</strong>
          </div>
        </v-col>
        <v-col v-if="item.lic_number" cols="12" md="4">
          <div class="certifi-item">
            شماره گواهی : <strong>{{ item.lic_number }}</strong>
          </div>
        </v-col>
        <v-col v-if="item.lic_file" cols="12" md="4">
          <div class="certifi-item">
            فایل گواهی : <strong><a :href="$store.state.host + item.lic_file" target="_blank" class="txtf--text">{{
              item.lic_file.substring(item.lic_file.lastIndexOf('/') + 1)
            }}</a></strong>
          </div>
        </v-col>
      </v-row>
    </div>
    <v-pagination
        v-if="items.total_objects"
        :value="options.page"
        v-model="options.page"
        color="info"
        class="mt-6"
        :length="parseInt(items.total_pages)"
    ></v-pagination>
    <p class="text-center  mb-2 mt-2 info--text" v-if="items.total_objects" style="font-size: 14px">
      {{ items.total_objects }} گواهی</p>
    <v-form ref="form">
      <v-row class="mt-12 mb-3" v-if="renderComponent">
        <v-col class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(data.title,'عنوان')]"
              v-model="data.title"
              color="txtf"
              label="عنوان"
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              v-model="data.reference"
              :rules="[$store.state.rules.required(data.reference,'مرجع')]"
              color="txtf"
              label="مرجع"
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(data.state,'وضعیت')]"
              v-model="data.state"
              color="txtf"
              label="وضعیت"
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(data.grade,'سطح')]"
              v-model="data.grade"
              color="txtf"
              label="سطح"
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              @keypress="isNumber($event)"
              :rules="[$store.state.rules.required(data.lic_number,'شماره گواهی')]"
              v-model="data.lic_number"
              color="txtf"
              label="شماره گواهی"
          ></v-text-field>
        </v-col>

        <v-col class="py-0" cols="12" md="4">
          <v-file-input
              outlined
              dense
              :rules="[$store.state.rules.required(lic_file,'فایل گواهی')]"
              v-model="lic_file"
              color="txtf"
              label="فایل گواهی"
          ></v-file-input>
        </v-col>

        <v-col
            class="py-0"
            cols="12"
        >
          <v-btn dark class="dash-btns dash-btns--green d-block mr-auto" height="32" @click="addTo()">
            <v-icon>mdi-plus</v-icon>
            افزودن گواهی
          </v-btn>
        </v-col>
      </v-row>
    </v-form>
  </v-card>
</template>

<script>
import axios from "axios";
import {forceRerender} from "@/components/Misc/forceRerender";
import {isChars} from "@/components/Misc/isChars";

export default {
  name: "ProfileCertificates",
  created() {
    this.fetchData();
  },
  mixins: [forceRerender,isChars],
  watch: {
    'options.page'() {
      this.fetchData();
    }
  },
  data() {
    return {
      loading: false,
      items: [],
      options : {
        page : 1
      },
      data: {
        title: null,
        reference: null,
        state: null,
        grade: null,
        lic_number: null,
      },
      lic_file: null,
    }
  },
  methods: {
    update() {
      axios.post(this.$store.state.host + '', {
        title: this.title,
        reference: this.reference,
        state: this.state,
        grade: this.grade,
        lic_number: this.lic_number,
      }, {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then()
    },
    deleteFromData(id) {
      this.loading = true
      axios.delete(this.$store.state.host + '/accounts/api/v1/remove-certificate/' + id, {
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(() => {
        this.notify('آیتم مورد نظر حذف شد.', 'info');
        this.fetchData();
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false
      })
    },
    fetchData() {
      this.loading = true
      axios.get(this.$store.state.host + '/accounts/api/v1/certificates/', {
        params : this.options,
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {
        this.items = response.data;
        console.log(this.items)
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false
      })
    },
    addTo() {
      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        this.loading = true;
        let formData = new FormData();
        Object.keys(this.data).forEach(key => {
          if (this.data[key]) {
            formData.append(key, this.data[key]);
          }
        })
        if (this.lic_file && (typeof this.lic_file === 'object')) {
          formData.append('lic_file', this.lic_file);
        }

        this.data = {
          title: null,
          reference: null,
          state: null,
          grade: null,
          lic_number: null,
        };
        this.lic_file = null;


        this.forceRerender();
        axios.post(this.$store.state.host + '/accounts/api/v1/add-certificate/', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            Accept: "*/*",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(() => {
          this.notify('گواهی اضافه شد', 'info');
          this.fetchData();
        }).catch(error => {
          this.errorHandle(error.response)
        }).finally(() => {
          this.loading = false
        })
      }
    }
  }

}
</script>

<style scoped>
.certifi-item {
  font-size: 14px;
  color: #615e5e;
}

.certifi-group {
  font-size: 14px;
  color: #615e5e;
  padding: 8px 12px;
  background-color: #f5f5f5;
  border-radius: 10px;
  border: 1px solid #eaeaea;
}
</style>
