<template>
  <div>
<!--    <p class="page-title mb-4">پروفایل</p>-->


    <v-row>
      <v-col cols="12" md="3">
        <div class="profile-card mb-3">
          <h5 class="profile-card-title mb-0">جزئیات پروفایل</h5>
          <div class="profile-card-body">
            <div class="d-flex flex-column justify-center align-center">
              <label style="cursor: pointer">
                <input accept=".jpeg,.jpg,.png" type="file" ref="file" style="display: none" @change="onFileChange()">
                <v-card elevation="0" :disabled="avatarLoading" :loading="avatarLoading" >
              <v-avatar
                  color="info"
                  class="mb-2"
                  size="128"
              >
                <img
                    v-if="avatar"
                    alt="Avatar"
                    :src="avatar"
                >
                <img
                    v-else-if="!avatar && !loading"
                    alt="Avatar"
                    style="object-fit: cover"
                    src="../../assets/image/empty-photo.png"
                >
                <v-skeleton-loader
                    v-else
                    width="100%"
                    type="card"
                ></v-skeleton-loader>
              </v-avatar>
                  <template v-slot:progress >
                    <GDLoading/>
                  </template>
                </v-card>
              </label>
              <p v-if="detials.first_name && detials.last_name" class="profile-card-title mb-0 pa-0">{{ detials.first_name }} {{ detials.last_name }}</p>
              <p v-else-if="detials.username" class="profile-card-title mb-0 pa-0">{{ detials.username }}</p>
              <p class="text-muted mb-2" v-if="detials.role && detials.role.title" >{{ detials.role.title }}</p>
            </div>
          </div>
          <div v-if="skills && skills.length" >
          <v-divider/>
          <div class="profile-card-body">
            <h5 class="profile-card-title mb-2 pa-0">مهارت ها</h5>
            <p class="profile-card-desc" ><span v-for="(item,index) in skills" :key="item.id+'skil'" ><span>{{item.title}}</span><span v-if="index!==(skills.length-1)" > , </span></span></p>
          </div>
          </div>
          <div v-if="detials.bio" >
            <v-divider/>
          <div  class="profile-card-body">
            <h5 class="profile-card-title mb-2 pa-0">بیوگرافی</h5>
            <p class="profile-card-desc" >{{detials.bio}}</p>
          </div>
          </div>
          <div v-if="detials.linkedin_link || detials.tel_link" >
            <v-divider/>
          <div class="profile-card-body">
            <h5 class="profile-card-title mb-2 pa-0">سایر ارتباطات</h5>
            <div class="d-flex justify-center align-center" >
            <a class="mx-1" v-if="detials.linkedin_link" :href="'https://instagram.com/'+detials.linkedin_link" target="_blank">
              <v-icon color="info" >mdi-instagram</v-icon> </a>
            <a class="mx-1" v-if="detials.tel_link" :href="'https://t.me/'+detials.tel_link" target="_blank">
              <v-icon color="info" style="transform: rotate(-45deg)" >mdi-send-circle</v-icon> </a>
            </div>
          </div>
          </div>
        </div>
      </v-col>
      <v-col cols="12" md="9" >
        <div class="profile-card mb-3">
          <v-tabs
              slider-color="info"
              icons-and-text
              v-model="tab"
              background-color="transparent"
              color="txtf"
              grow
          >
            <v-tab
                v-for="(item,i) in items"
                :key="i+'pItmes'"
            >
              {{ item.text }}
              <v-icon>{{ item.icon }}</v-icon>
            </v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab">
            <v-tab-item
                v-for="(item,i) in items"
                :key="i+'titems'"
            >
              <v-card
                  flat
                  :disabled="loading"
                  elevation="0"
              >
                <div style="padding: 3rem 1.25rem">
                  <ProfileDetails :cdata="detials" v-if="i===0" />
                  <ProfileSkills style="min-height: 482px" v-if="i===1" />
                  <ProfileSkills latin="honorr" label="افتخار" add-api="/accounts/api/v1/add-honor/" delete-api="/accounts/api/v1/remove-honor/" fetch-api="/accounts/api/v1/honors/" style="min-height: 482px" v-if="i===2" />
                  <ProfileCertificates style="min-height: 482px" v-if="i===3"/>
                </div>
              </v-card>
            </v-tab-item>
          </v-tabs-items>

        </div>
      </v-col>
    </v-row>

  </div>
</template>

<script>

import ProfileDetails from "@/components/Dashboard/Profile/Details";
import ProfileSkills from "@/components/Dashboard/Profile/ProfileSkills";
import ProfileCertificates from "@/components/Dashboard/Profile/ProfileCertificates";
import axios from "axios";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
export default {
  name: "ProfileApp",
  components: { GDLoading, ProfileCertificates, ProfileSkills, ProfileDetails},
  created() {
    this.fetchProfile();
    this.fetchSkillData();
  },
  data () {
    return {
      profileData : {
        dialog : false,
        idSelected : null,
      },
      tab: null,
      loading : false,
      avatar : null,
      avatarLoading : false,
      skills : [],
      detials : {
        first_name : null,
        last_name : null,
        username : null,
        email : null,
        role : null,
        bio : null,
        address : null,
        linkedin_link : null,
        nick_name : null,
        tel_link : null,
        website_link : null,
        zip_code : null,
        mobile:null,
      },
      items: [
        {text:'مشخصات',icon : 'mdi-account-box-outline'},
        {text:'مهارت ها',icon : 'mdi-shape-circle-plus'},
        {text:'افتخارات',icon : 'mdi-octagram-outline'},
        {text:'گواهی ها',icon : 'mdi-certificate'},
      ],
      text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',
    }
  },
  methods : {
    fetchSkillData(){
      this.loading= true
      axios.get(this.$store.state.host + '/accounts/api/v1/skills/',{
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.skills = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false
      })
    },
    onFileChange() {
      const file = this.$refs.file.files[0];
      if(file.size>2097152) {
        this.notify('حجم عکس بیشتر از 2 م.گ','error')
        return
      }
      if((file.type!=='image/jpeg') && (file.type!=='image/jpg') && (file.type!=='image/png')) {
        this.notify('فرمت غیر قابل پشتیبانی','error')
        return
      }
      let formData = new FormData();
      formData.append('avatar', file);
      this.avatarLoading = true;
      axios.patch(this.$store.state.host+'/accounts/api/v1/user-update/', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(() => {
            this.fetchProfile();
          }
      ).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.avatarLoading = false;
      })
    },
    fetchProfile() {
      this.loading = true;
      this.avatar = null;
      axios.get(this.$store.state.host + '/accounts/api/v1/user/',{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        const data = response.data.data;
        this.avatar = data.avatar;
        this.$store.state.profile.avatar = this.avatar;
        this.$cookie.set("profile", JSON.stringify(this.$store.state.profile));
        this.detials.first_name = data.first_name;
        this.detials.last_name = data.last_name;
        this.detials.username = data.username;
        this.detials.email = data.email;
        this.detials.role = data.role;
        this.detials.bio = data.bio;
        this.detials.address = data.address;
        this.detials.linkedin_link = data.linkedin_link;
        this.detials.tel_link = data.tel_link;
        this.detials.website_link = data.website_link;
        this.detials.zip_code = data.zip_code;
        this.detials.nick_name = data.nick_name;
        this.detials.mobile = data.mobile;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false;
        this.avatarLoading = false;
      })
    }
  }
}
</script>

