<template>
  <v-card elevation="0" :disabled="loading" >
    <v-form ref="form" >
      <v-row>
        <v-col class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(data.first_name,'نام')]"
              v-model="data.first_name"
              color="txtf"
              label="نام"
          ></v-text-field>

        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              v-model="data.last_name"
              :rules="[$store.state.rules.required(data.last_name,'نام خانوادگی')]"
              color="txtf"
              label="نام خانوادگی"
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              v-model="data.nick_name"
              dense
              color="txtf"
              label="نام مستعار"
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <div class="dir-ltrF" >
            <v-text-field
                outlined
                dense
                readonly
                v-model="data.username"
                value=" "
                color="txtf"
                label="نام کاربری"
            ></v-text-field>
          </div>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <div class="dir-ltrF" >
            <v-text-field
                outlined
                dense
                v-model="data.email"
                value=" "
                readonly
                color="txtf"
                label="ایمیل"
            ></v-text-field>
          </div>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              v-if="data.role"
              readonly
              v-model="data.role.title"
              value=" "
              color="txtf"
              label="نقش"
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <div class="dir-ltrF" >
            <v-text-field
                outlined
                dense
                @keypress="isNumber($event)"
                :rules="[$store.state.rules.tellPattern(data.mobile)]"
                v-model="data.mobile"
                color="txtf"
                label="شماره همراه"
            ></v-text-field>
          </div>
        </v-col>


        <v-col class="py-0" cols="12" md="4">
          <div class="dir-ltrF" >
            <v-text-field
                outlined
                dense
                v-model="data.tel_link"
                color="txtf"
                label="تلگرام"
            >
              <template v-slot:append>
                <p class="mb-0 append-label" >@</p>
              </template>
            </v-text-field>
          </div>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <div class="dir-ltrF" >
            <v-text-field
                outlined
                dense
                v-model="data.linkedin_link"
                color="txtf"
                label="اینستاگرام"
            >
              <template v-slot:append>
                <p class="mb-0 append-label" >@</p>
              </template>
            </v-text-field>
          </div>
        </v-col>

        <v-col class="py-0" cols="12" md="12">
          <div class="dir-ltrF" >
            <v-text-field
                outlined
                dense
                v-model="data.website_link"
                color="txtf"
                label="وب سایت"
            >
              <template v-slot:append>
                <p class="mb-0 append-label" >//:https</p>
              </template>
            </v-text-field>
          </div>
        </v-col>

        <v-col class="py-0" cols="12" md="8">
          <v-text-field
              outlined
              dense
              v-model="data.address"
              color="txtf"
              label="آدرس"
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="4">
          <div class="dir-ltrF" >
            <v-text-field
                outlined
                v-model="data.zip_code"
                dense
                color="txtf"
                label="کدپستی"
            ></v-text-field>
          </div>
        </v-col>

        <v-col class="py-0" cols="12" md="12">
          <v-textarea
              outlined
              dense
              v-model="data.bio"
              rows="3"
              color="txtf"
              label="بیوگرافی"
          ></v-textarea>
        </v-col>

        <v-col
            class="py-0"
            cols="12"
        >
          <v-btn :loading="loading" dark class="dash-btns d-block ml-auto" color="txtf" height="32" @click="update()" >ذخیره تغییرات</v-btn>
        </v-col>
      </v-row>
    </v-form>
  </v-card>
</template>

<script>
import axios from "axios";
import {isChars} from "@/components/Misc/isChars";

export default {
  name: "ProfileDetails",
  mixins : [isChars],
  props : ["cdata"],
  data () {
    return {
      data : this.cdata,
      loading : false,
    }
  },
  methods : {
    update() {
      this.$refs.form.validate();
      if(this.$refs.form.validate()){
        this.loading = true;
        axios.patch(this.$store.state.host + '/accounts/api/v1/user-update/',{
          mobile : this.data.mobile,
          username : this.data.username,
          first_name : this.data.first_name,
          last_name : this.data.last_name,
          bio : this.data.bio,
          address : this.data.address,
          linkedin_link : this.data.linkedin_link,
          nick_name : this.data.nick_name,
          tel_link : this.data.tel_link,
          website_link : this.data.website_link,
          zip_code : this.data.zip_code

        },{
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(()=>{
          this.notify('جزئیات پروفایل بروز شد','info');
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(()=>{
          this.loading = false;
        })
      }
    }
  }
}
</script>

