<template>
  <v-card elevation="0" :disabled="loading">
    <v-row class="pa-3">
      <v-col class="py-1" v-for="(item,index) in items" :key="index+'skillsss'" cols="12" md="6">
        <div class="d-flex justify-space-between align-center skill-box">
          <p class="ma-0 txtf--text" >{{ item.title }}</p>
          <v-btn  icon>
            <v-icon class="skill-btn" color="error" @click="deleteFromData(item.id)">mdi-close</v-icon>
          </v-btn>
        </div>
      </v-col>
    </v-row>
    <v-row class="pa-3">
      <v-col cols="12" md="6" >
        <v-form ref="form" v-if="renderComponent">
        <v-text-field
            :placeholder="label +' جدید '"
            v-model="title"
            :rules="[$store.state.rules.required(title,'عنوان')]"
            @keydown.prevent.enter="addTo"
            color="txtf"
        >
          <template v-slot:append>
            <v-icon style="cursor: pointer" @click="addTo()" color="success" >mdi-plus</v-icon>
          </template>

        </v-text-field>
        </v-form>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>

import axios from "axios";
import {forceRerender} from "@/components/Misc/forceRerender";

export default {
  name: "ProfileSkills",
  mixins : [forceRerender],
  props : {
    label : {
      default : 'مهارت'
    },
    latin : {
      default : 'skillsss'
    },
    deleteApi : {
      default: '/accounts/api/v1/remove-skill/'
    },
    addApi : {
      default: '/accounts/api/v1/add-skill/'
    },
    fetchApi : {
      default: '/accounts/api/v1/skills/'
    }
  },
  created() {
    this.fetchData();
  },
  components: {},
  data () {
    return {
      title : null,
      loading : false,
      items : [],
    }
  },
  methods : {
    deleteFromData(id){
      this.loading= true
      axios.delete(this.$store.state.host + this.deleteApi +id,{
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(()=>{
        this.notify('آیتم مورد نظر حذف شد.','info');
        this.fetchData();
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false
      })
    },
    fetchData(){
      this.loading= true
      axios.get(this.$store.state.host + this.fetchApi,{
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.items = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false
      })
    },
    addTo(){
      this.$refs.form.validate();
      if(this.$refs.form.validate()){
        this.loading = true;
        const title = this.title;
        this.title = null;
        this.forceRerender();
        axios.post(this.$store.state.host + this.addApi ,{
          title
        }, {
          headers: {
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(()=>{
          this.fetchData();
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(()=>{
          this.loading = false
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.skill-box {
  border: 1px solid #ececec;
  border-radius: 5px;
  padding: 2px 8px;
  .skill-btn {
    display: none;
  }
  &:hover {
    background-color: #ececec;
    .skill-btn {
      display: block;
    }
  }
}
</style>